<template>
  <div class="hot-product">
    <!--<mt-loadmore :bottom-method="loadBottom" :autoFill="false" :bottom-all-loaded="allLoaded" ref="loadmore">-->
    <div class="product-content-wrapper">
      <img class="img-responsive" src="../assets/openHotProduct.png" />
      <div class="product-content">
        <div v-for="(item, index) in productList" :key="index">
          <ProductItem :product="item"></ProductItem>
        </div>
        <p class="product-no-tip" v-if="noProductFlag">{{noProductTip}}</p>
      </div>
    </div>
    <div class="licai-wrapper" @click="gotoLicai">
      <p>去理财商城逛逛>></p>
    </div>
    <div class="heart-tip">
      <p>
        风险提示：相关基金展示不构成对产品的推荐，以上所提供信息仅供参考，投资者应当自主投资决策，对投资者因依赖上述信息进行投资决策而导致的投资分险由投资者自行承担。投资有风险，应保持谨慎。
      </p>
    </div>
    <div class="gift-tip">
      <p>投资有风险 入市需谨慎</p>
    </div>
    <!-- </mt-loadmore>-->
  </div>
</template>
<script>

import { getProductList, getAllProduct } from '@/api/product'
import { Toast, MessageBox, Indicator, Loadmore } from 'mint-ui';
import ProductItem from '@/components/ProductItem.vue'
import jsAPI from '../libs/jsAPI'
export default {
  name: 'product',
  components: {
    ProductItem
  },
  data() {
    return {
      productList: [],
      noProductFlag: false,
      noProductTip: '没有热门产品',
      allLoaded: false,
      pageNumber: 0
    }
  },
  computed: {

  },
  created() {
    let titleObject = {
      back: true,
      romoveMarginTop: false,
      title: "开门红热销产品"
    }
    this.$emit("routerChange", titleObject);
    this.getAllProduct();
  },
  methods: {
    getAllProduct() {
      Indicator.open({
        text: '加载中...',
        spinnerType: 'triple-bounce'
      });
      getAllProduct().then(res => {
        Indicator.close();
        if (res.data.resCode == 0) {
          //成功请求数据
          if (res.data.result.length > 0) {
            this.productList = res.data.result;
          }
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 2000
          });
        }
      });
    },
    getProductList() {
      Indicator.open({
        text: '加载中...',
        spinnerType: 'triple-bounce'
      });
      let params = {
        pageNumber: this.pageNumber,
        pageSize: 10
      }
      getProductList(params).then((res) => {
        console.log(res);
        Indicator.close();
        if (res.data.resCode == 0) {
          //成功请求数据
          if (res.data.result.length > 0) {
            this.productList = this.productList.concat(res.data.result);
            this.noProductFlag = false;
          } else {
            this.noProductFlag = true;
          }
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 2000
          });
        }
      });
    },
    loadBottom: function () {
      console.log("loadBottom");
      this.pageNumber += 1;
      this.getProductList();
    },
    gotoLicai: function () {
      jsAPI.forwardPageByAction('forwardShangcheng');
    }
  }
}
</script>
